
.comment-list {
  height: 100%;
}
.comment-list .full-star {
  margin: 10px 0 10px 0;
}
.pucl {
  position: fixed;
  bottom: 47px;
  width: 100%;
  background: #fff;
  padding: 15px 0;
}
.pucl .xx {
  height: 36px;
  line-height: 36px;
}
.pucl .xx label {
  margin-left: 12px;
  margin-right: 10px;
}
.pucl .pub {
  display: block;
  width: 94%;
  height: 50px;
  margin: 0 auto;
  border: 1px solid #f1f1f1;
  border-radius: 5px;
  padding: 5px 5px;
}
.pucl .pub-line {
  background: #f2f2f2;
  height: 10px;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  margin-bottom: 20px;
}
.comment {
  padding: 0 10px 0;
}
.comment.pb150 {
  padding-bottom: 150px;
}
.comment .c-h {
  height: 46px;
  line-height: 46px;
  color: #81838e;
  border-bottom: 1px solid #f1f1f1;
}
.comment .c-h a {
  color: #81838e;
}
.comment .c-h em {
  font-style: normal;
}
.comment-list-item {
  padding: 0 0 10px 0;
  border-bottom: 1px solid #f1f1f1;
}
.comment-item-data {
  color: #848689;
  font-size: 12px;
}
.comment .replay {
  display: inline;
  padding: 2px 4px;
}
.comment-item-info {
  height: 35px;
  line-height: 35px;
  color: #f23030;
}
.full-star {
  margin-top: 10px;
  width: 70px;
  height: 14px;
  line-height: 20px;
  background-size: auto 100%;
  color: #f15353;
  background-image: url("../assets/images/nostart.svg");
}
.score-start {
  width: 100%;
  height: 100%;
  background-size: auto 100%;
  background-image: url("../assets/images/star.svg");
}
.full-star .score {
  font-size: 12px;
}
.comment-item-author {
  color: #848689;
  font-size: 12px;
}
.comment-item-content {
  color: #333;
  padding-bottom: 5px;
  font-size: 13px;
  line-height: 20px;
}



