
.pr {
  position: relative;
}
.fl,
.fr {
  display: inline;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
img {
  padding: 0;
  margin: 0;
}
.goods-arctile{ min-height:
}
.add-to-pub{ margin: 10px 0 0 10px;
}
.goods-arctile .biger-img {
  border-bottom: 1px solid #f1f2f4;
  width: 100%;
}
.goods-arctile .biger-img img {
  width: 100%;
  vertical-align: middle;
}
.goods-arctile .info {
  padding: 10px;
  color: #333;
  width: 100%;
}
.goods-arctile .b-hr {
  height: 10px;
  background: #f0f2f5;
  border-top: 1px solid #e9ebef;
  border-bottom: 1px solid #e9ebef;
}
.goods-arctile .info .name {
  font-size: 16px;
  margin-bottom: 3px;
  padding: 0 5px;
}
.goods-arctile .info .describe {
  font-size: 14px;
  padding: 0 5px;
}
.goods-arctile .info .price {
  padding: 8px 0;
  font-size: 24px;
  color: #f23030;
}
.comment {
  padding: 0 10px;
}
.comment .c-h {
  height: 46px;
  line-height: 46px;
  color: #81838e;
  border-bottom: 1px solid #f1f1f1;
}
.comment .c-h a {
  color: #81838e;
}
.comment .c-h em {
  font-style: normal;
}
.comment-list-item {
  padding: 0 0 10px 0;
  border-bottom: 1px solid #f1f1f1;
}
.comment-item-info {
  height: 35px;
  line-height: 35px;
  color: #f23030;
}
.full-star {
  margin-top: 10px;
  width: 70px;
  height: 14px;
  line-height: 20px;
  background-size: auto 100%;
  color: #f15353;
  background-image: url("../assets/images/nostart.svg");
}
.score-start {
  width: 100%;
  height: 100%;
  background-size: auto 100%;
  background-image: url("../assets/images/star.svg");
}
.full-star .score {
  font-size: 12px;
}
.comment-item-author {
  color: #848689;
  font-size: 12px;
}
.comment-item-content {
  color: #333;
  padding-top: 0px;
  font-size: 13px;
  line-height: 20px;
}



