/* HTML & Body reset */
html, body {
  width: 100%;
  height: 100%;
}

body {
  background-color: #FFF;
  color: #3F536E;
  line-height: 1.5;
  font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
}

/* Unify the margin and padding */
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td, hr, button, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  margin: 0;
  padding: 0;
}

/* Reset fonts for relevant elements */
button, input, select, textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
}

ul, ol {
  list-style: none;
}

/* Remove the clear button of a text input control in IE10+ */
input::-ms-clear, input::-ms-reveal {
  display: none;
}

::-moz-selection {
  background: #6190E8;
  color: #fff;
}

::selection {
  background: #6190E8;
  color: #fff;
}

/* Link */
a {
  color: #6190E8;
  background: transparent;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  -webkit-transition: color .3s ease;
  transition: color .3s ease;
}

a:hover {
  color: #79A1EB;
}

a:active {
  color: #4F7DE2;
}

a:hover, a:active {
  outline: 0;
  text-decoration: none;
}

a[disabled] {
  color: #BFBFBF;
  cursor: not-allowed;
  pointer-events: none;
}

/* Code Block */
code, kbd, pre, samp {
  font-family: Consolas, Menlo, Courier, monospace;
}

/* Utility classes */
.clearfix::after {
  clear: both;
  content: '';
  display: block;
}

.show {
  display: block !important;
}

.hide {
  display: none !important;
}

.invisible {
  visibility: hidden !important;
}

.pull-left {
  float: left !important;
}

.pull-right {
  float: right !important;
}

/* Title */
h1, h2, h3, h4, h5, h6 {
  color: #2C405A;
}

h1 {
  font-size: 20px;
}

h2 {
  font-size: 18px;
}

h3 {
  font-size: 16px;
}

h4, h5, h6 {
  font-size: 14px;
}

hr {
  margin: 1.2em 0 1.5em;
}

/* Text */
p {
  color: #3F536E;
  font-size: 14px;
}

.text-smallest {
  font-size: 11px;
}

.text-smaller {
  font-size: 12px;
}

.text-small {
  font-size: 13px;
}

.text-base {
  font-size: 14px;
}

.text-normal {
  font-size: 16px;
}

.text-large {
  font-size: 18px;
}

.text-larger {
  font-size: 20px;
}

