
.wrap{width: 100%; height: 100%; display: -webkit-box; display: -ms-flexbox; display: flex; -webkit-box-orient: vertical; -webkit-box-direction: normal; -ms-flex-direction: column; flex-direction: column; background: #fff;
}
.mian.cc{ -webkit-box-flex: 1; -ms-flex: 1; flex: 1;
}
.main-back-TAB .el-tabs__nav{ width: 100%; display: -webkit-box; display: -ms-flexbox; display: flex; font-size: 12px; height: 40px; line-height: 40px; border-bottom: 1px solid #ededed;
}
