}
.main-back-TAB{ width: 100%; height: 100%;
}
.main-back-TAB .el-tabs__header{ margin-bottom: 10px;
}
.main-back-TAB .el-tabs__content{ height: -webkit-calc(100% - 51px); -webkit-overflow-scrolling: touch;overflow: auto;
}
