
.footer-bar {
  height: 46px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 20px;
  border-top: 1px solid #ededed;
  background: #fff;
}
.footer-bar a.al {
  font-size: 12px;
  color: #333;
  padding-top: 5px;
  text-align: center;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 12px;
}
.footer-bar a.al span {
  font-size: 20px;
  line-height: 20px;
  display: block;
}
.footer-bar a.al span:nth-child(2) {
  font-size: 12px;
}
.footer-bar a.al span.icon-user {
  font-size: 18px;
}
.footer-bar a.al span .o{
	border-radius: 10px;
	padding: 0 2px;
	background: #f40000;
	font-size: 12px;
	font-style: normal;
	vertical-align: middle;
	text-align: center;
	color: #ffffff;
}
.footer-bar a.al.router-link-exact-active{
   color: #f40000;
   background: #f7f7f7;
} 



