
.add_address{ display: -webkit-box; display: -ms-flexbox; display: flex; -webkit-box-orient: vertical; -webkit-box-direction: normal; -ms-flex-direction: column; flex-direction: column; height: 100%; background: #f2f2f2;
}
.add_address .header{ background: #fff; height: 48px; line-height: 48px; color: #333; text-align: center; font-size: 20px; border-bottom: 1px solid #ddd; margin-bottom: 10px; position: relative;
}
.add_address .header i{ position: absolute; left: 0px; top: 0px; width: 38px; height: 48px;  line-height: 48px;
}
.add_address .add_address_main{ background: #fff; border-top: 1px solid #ddd;
}
.add_address .add_address_input{ height: 38px; line-height: 38px; font-size: 12px; border-bottom: 1px solid #e6e6e6; margin: 0 10px; display: -webkit-box; display: -ms-flexbox; display: flex; position: relative;
}
.add_address .add_address_input label{ color: #666; width: 62px; text-align: right; margin-right: 5px;
}
.add_address .add_address_input input{ -webkit-box-flex: 1; -ms-flex: 1; flex: 1; color: #666; height: 36px; line-height: 36px; border: none; -webkit-appearance: none; outline: none;
}
.add_address .add_address_input .xx{color: #666; -webkit-box-flex: 1; -ms-flex: 1; flex: 1;
}
.add_address .add_address_input .xx.xr{ text-align: right;
}
.add_address .add_address_input .xx em{ font-style: normal;
}
.add_address .add_address_input .xx i{ margin-left: 5px; margin-top: 12px; font-size: 12px; float: right;
}
.address_save{ border-radius: 10px; width: 90%; margin: 20px auto; background: #ff5c5c; color: #fff; text-align: center; height: 42px; line-height: 42px; text-align: center;
}



