
.demo-table-expand {
  font-size: 0;
  border-bottom: 1px solid #dfe6ec;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 100%;
}
.e-html {
  position: relative;
  display: block;
}
.e-pr {
  float: right;
}
.e-discount-input::-webkit-input-placeholder {
  color: #c0c0c0;
}
.e-discount-input {
  position: absolute;
  right: 0px;
  width: 60px;
  height: 24px;
  line-height: 24px;
  border: 1px solid #bfcbd9;
  border-radius: 5px;
  text-align: center;
  font-size: 12px;
}
.el-message-box {
  width: 320px;
}



