
@font-face {
  font-family: "iconfont";
  src: url('assets/images/iconfont.eot?t=1507778414380#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('assets/images/iconfont.ttf?t=1507778414380') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
  url('assets/images/iconfont.svg?t=1507778414380#iconfont') format('svg'); /* iOS 4.1- */
}
.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.al-icon-user:before { content: "\e843";
}
.al-icon-home:before { content: "\e70a";
}
.al-icon-cart:before { content: "\e61f";
}
.al-icon-dagou:before { content: "\e660";
}
*{ margin: 0; padding: 0;
}
html,body{ width: 100%; height: 100%; color: #333;
}
#app{ width: 100%; height: 100%;
}
.fade-enter-active, .fade-leave-active {
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
.fade-enter {
    opacity: 1;
    -webkit-transform: translate3d(0, -20px, 0);
            transform: translate3d(0, -20px, 0);
}
.fade-leave-active {
    opacity: 0;
    -webkit-transform: translate3d(-30%, 0, 0);
            transform: translate3d(-30%, 0, 0);
}
.app-content{  width: 100%; height: 100%; position: relative; z-index: 2;
}
.loader{
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   width: 100%;
   height: 100%;
   position: absolute;
   left: 0;
   top: 0;
   z-index: 1;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.ball-spin-fade-loader {
  position: relative;
}
@-webkit-keyframes ball-spin-fade-loader {
50% {
    opacity: 0.3;
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
}
100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes ball-spin-fade-loader {
50% {
    opacity: 0.3;
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
}
100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
