
.buy-cart {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #f7f7f7;
}
.buy-cart .corm {
  padding: 15px 15px;
}
.buy-cart .header {
  position: relative;
}
.buy-cart .e-dd {
  font-size: 14px;
  color: #9c9c9c;
  position: absolute;
  right: 0px;
  width: 68px;
}
.buy-cart-list .dogo {
  width: 30px;
  height: 88px;
  cursor: pointer;
}
.buy-cart-list .dogo i {
  display: block;
  width: 15px;
  height: 15px;
  border: 1px solid #9e9e9e;
  border-radius: 50%;
  margin: 38px auto 0;
  font-size: 10px;
  color: #fff;
  position: relative;
}
.buy-cart-list .dogo i:before {
  top: -1px;
  position: absolute;
}
.buy-cart-list .dogo.active i {
  background: #e93b3d;
}
.buy-cart .header {
  background: #fff;
  height: 42px;
  line-height: 42px;
  text-align: center;
  border-bottom: 1px solid #ededed;
  font-size: 18px;
}
.buy-cart-list {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  -webkit-overflow-scrolling: touch;
  overflow: auto;
  padding-bottom: 10px;
}
.buy-cart-list ul li {
  border-top: 1px solid #ededed;
  border-bottom: 1px solid #ededed;
  background: #fff;
  margin-top: 10px;
}
.buy-cart-list ul li a {
  margin: 0 0;
  padding: 10px 15px 5px 0px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.buy-cart-list ul li .img {
  width: 88px;
  margin-right: 10px;
}
.buy-cart-list ul li .img img {
  width: 100%;
}
.buy-cart-list ul li .info {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.buy-cart-list .t-n {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  color: #333;
  font-size: 16px;
}
.buy-cart-list .describe {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  color: #9c9c9c;
  line-height: 18px;
}
.buy-cart-list .price {
  color: #dd2727;
  font-size: 16px;
  margin-top: 8px;
}
.buy-cart-list .sl {
  float: left;
  line-height: 28px;
}
.buy-cart-list .sum {
  float: right;
}
.buy-cart-list .sum .el-input-number--small {
  width: 86px;
}
.buy-cart-list .el-input-number__decrease,
.buy-cart-list  .el-input-number__increase {
  width: 24px;
  line-height: 22px;
  font-size: 12px;
}
.el-input-number--small .el-input-number__increase {
  width: 24px;
  line-height: 22px;
  font-size: 12px;
}
.buy-cart-list .el-input-number__decrease {
  right: 24px;
}
.buy-cart-list .el-input-number--small .el-input__inner {
  padding-right: 48px;
  height: 24px;
  line-height: 24px;
}
.amount .amount-mov {
  height: 38px;
  line-height: 38px;
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-top: 1px solid #ededed;
  -webkit-box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
}
.amount .am-c {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-left: 8px;
  font-size: 12px;
}
.amount .am-c .aml {
  float: left;
  width: 60px;
  cursor: pointer;
}
.amount .am-c .aml i {
  float: left;
  width: 15px;
  height: 15px;
  border: 1px solid #9e9e9e;
  border-radius: 50%;
  margin-top: 10px;
  margin-right: 8px;
  color: #fff;
  font-size: 12px;
  position: relative;
}
.amount .am-c .aml i:before {
  position: absolute;
  left: 0;
  top: -13px;
}
.amount .am-c .amr {
  float: right;
}
.amount .am-c .amr em {
  font-style: normal;
  margin-right: 10px;
  color: #f30505;
}
.amount .am-b {
  width: 86px;
  background: #f30505;
  color: #fff;
  font-size: 14px;
  text-align: center;
}
.amount .am-c.active .aml i {
  background: #e93b3d;
}




