
.all-goods-list{ height: 100%;  -webkit-overflow-scrolling : touch; overflow: auto;
}
.all-goods-list ul li{  border-bottom: 1px solid #ededed;
}
.all-goods-list ul li a{ margin: 0 0; padding: 10px 15px 5px; display: -webkit-box; display: -ms-flexbox; display: flex;
}
.all-goods-list ul li .img{ width: 88px; margin-right: 10px;
}
.all-goods-list ul li .img img{ width: 100%;
}
.all-goods-list ul li .info{ -webkit-box-flex: 1; -ms-flex: 1; flex: 1;
}
.all-goods-list .t-n{
	overflow : hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical;
	color: #333;
	font-size: 16px;
}
.all-goods-list .describe{
	overflow : hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	color: #9c9c9c;
	line-height: 18px;
}
.all-goods-list .price{ color: #DD2727; font-size: 16px;
}



