
.front-acticle-details{
}
.front-acticle-details .content{ margin: 0 10px 15px;
}
.front-acticle-details .content img{ width: 100%;
}
.details-line {
  color: #9a9a9a;
  position: relative;
  height: 24px;
  line-height: 24px;
  margin: 10px 0;
}
.details-line::before {
  content: "";
  position: absolute;
  height: 1px;
  background: #f1f1f1;
  width: 100%;
  top: 12px;
}
.details-line span {
  width: 100px;
  margin: 0 auto;
  background: #ffffff;
  display: block;
  height: 24px;
  text-align: center;
  position: relative;
  z-index: 1;
}



