
img{ vertical-align: top; margin: 0; padding: 0;
}
.pr {
  position: relative;
}
.fl,
.fr {
  display: inline;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.wrap {
  width: 100%;
  height: 100%;
  background: #fff;
}
.mian.cc {
  height: -webkit-calc(100% - 28.169% - 46px);
}
.main-TAB .el-tabs__nav {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 12px;
  height: 42px;
  line-height: 42px;
  border-bottom: 1px solid #ededed;
  -webkit-overflow-scrolling: touch;
  overflow: auto;
}
.main-TAB .el-tabs__nav .el-tabs__item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #333;
}
.mian {
  width: 100%;
}
.main-TAB {
  height: 100%;
}
.main-TAB .el-tabs__header {
  margin-bottom: 0px;
}
.main-TAB .el-tabs__content {
  height: -webkit-calc(100% - 40px);
}
.main-TAB .el-tabs__content .el-tab-pane {
  height: 100%;
}
.footer-bar {
  width: 100%;
  height: 48;
  line-height: 48px;
  color: #333;
  border-top: 1px solid #ededed;
}
.quit {
  padding: 0 20px;
  float: right;
}
.main-cont {
  padding: 0 0;
}
.main-cont .el-tabs__active-bar {
  background-color: #000;
}
.page-loading {
  top: 50%;
  width: 100%;
  position: fixed;
}



